* {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin-left: 20px;
}

.nav__item-ap {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link.is-active {
  color: #00acdc;
  margin: 0;
}

.nav__link.is-active:after {
  content: "";
  color: #00acdc;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: 56px;
  position: absolute;
}

.nav__link:before {
  content: "";
  color: #00acdc;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: 56px;
  position: absolute;
  transform: scaleX(0);
}

.nav__link:hover {
  color: #00acdc;
}

.container {
  box-sizing: border-box;
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

.product-card:hover .product-card__title {
  color: #34569b;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: flex;
}

.product-card__description {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.product-card__rating-reviews {
  color: #060b35;
  align-items: center;
  font-size: 10px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.product-card__price-name {
  color: #616070;
  font-size: 12px;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: bold;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

